


.slide-container {
    width: 90%;
 
    padding: 0;
    margin: auto;
  
}
.slide-container-boxed {
    width: 90%;
    padding: 0;
    height: 50%;
    margin-top:5%;
    margin-left: auto;
    margin-right:auto;

}
h3 {
    text-align: center;
    font-weight:bold;
    color:white;
}
.image-test {
  
    background-position: center;
    background-size: cover;
    margin: 0;
    
}
.each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
    background-repeat:no-repeat;
    height: 50vh;
}

.each-slide-boxed {
    height: 100%;
   
    width:100%;
}
.slide-box {
    height: 100%;
  
}
.removed-from-below {
    display: flex;
    justify-content: center;
    align-items: center;
}
.each-slide-boxed > div {
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 270px;
    width:100%;
}

.each-slide span {
    padding: 20px;
    font-size: 20px;
    width: 50%;
    text-align: center;
}
.each-slide-boxed span {

    padding: 1px;
    font-size: 10px;
    text-align: center;

}
.each-fade {
    display: flex;
}

    .each-fade .image-container {
        width: 75%;
        overflow: hidden;
    }

        .each-fade .image-container img {
            width: 100%;
        }

    .each-fade h2 {
        width: 25%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0;
        background: #adceed;
    }
