.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.bg-image{
    background-image: url('./img_green_iron.jpg');
    background-position: center;
    background-size: cover;
    overflow-x:hidden;
    margin: 0;
    height: 100vh;
}
.bg-image-width-sf {
    background-image: url('./img_store-front.jpg');
    background-position: center;
    background-size: cover;
    overflow-x:hidden;
    margin-left: auto;
    margin-right: auto;
    margin-top:20px;
    margin-bottom:20px;
    width: 100%;
    height: 50vh;
}
.sh-box{
    height: 100vh;
    width: 100vw;
 
}
.bg-image-width-sf-boxed {
    background-image: url('./img_store-front.jpg');
    background-position: center;
    background-size: cover;
    overflow-x: hidden;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
    width: 100%;
    height: 40%;
}
.bg-image-width-bb {
    background-image: url('./baconeggbap.jpg');
    background-position: center;
    background-size: cover;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100%;
    height: 350px;
}
.font-style{
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size:20px;
}